@charset "UTF-8";
.wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-template-rows: repeat(auto-fill, minmax(255px, 1fr));
  margin: 0 auto;
  gap: 30px; }

.section-preview {
  padding: 37px 0; }

.section-preview__item {
  overflow: hidden; }

.section-preview__img {
  max-height: 300px; }

.section-preview__title {
  margin: 0;
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 25px;
  text-align: left;
  margin-bottom: 20px;
  color: #202020; }

.section-preview__item img {
  width: 100%;
  height: 100%;
  object-fit: cover; }

.section-preview__item.one {
  box-sizing: border-box;
  position: relative;
  display: flex;
  flex-direction: column;
  max-height: 633px;
  align-items: flex-start;
  padding: 65% 60px 30px;
  grid-column: 1/3;
  grid-row: 1/3;
  background: url("../../img/1.jpg") no-repeat;
  background-size: cover;
  background-position: center; }

.section-preview__item.one::after {
  background-color: #333;
  content: "#мастеркласс";
  padding: 7px 15px;
  display: block;
  position: absolute;
  right: 15px;
  top: 17px;
  font-family: Roboto;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px; }

.section-preview__item:nth-child(2) {
  grid-column: 3/5;
  grid-row: 1/2; }

.section-preview__item:nth-child(3) {
  grid-column: 3/4;
  grid-row: 2/3;
  max-height: 255px; }

.section-preview__item:nth-child(4) {
  grid-column: 4/5;
  max-height: 255px; }

.section-preview__info {
  margin: 0;
  font-family: Roboto;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px; }

.section-preview__desc {
  margin: 0;
  font-weight: 700;
  font-size: 36px;
  line-height: 37px;
  max-width: 427px;
  margin-bottom: 19px; }

@media (max-width: 1340px) {
  .wrapper {
    max-width: 100%;
    grid-template-columns: repeat(auto-fit, minmax(230px, 1fr));
    grid-template-rows: repeat(auto-fill, minmax(332px, 1fr)); }
  .section-preview__item img {
    object-fit: cover;
    max-height: 100%;
    max-width: 100%; }
  .section-preview__item.one {
    max-height: 100%; } }

@media (max-width: 1198px) {
  .wrapper {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 400px auto; }
  .section-preview__item.one {
    padding: 65% 11px 30px 28px; } }

@media (max-width: 768px) {
  .section-preview__title {
    font-size: 22px;
    line-height: 23px; }
  .wrapper {
    grid-template-rows: 291px auto; }
  .section-preview__desc {
    font-size: 34px;
    line-height: 31px;
    max-width: 239px;
    margin-bottom: 24px; }
  .section-preview__info {
    margin-bottom: 21px; } }

@media (max-width: 620px) {
  .section-preview {
    padding: 37px 0 25px; }
  .wrapper {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto; }
  .section-preview__item.one {
    grid-column: 1/3;
    grid-row: 1/2; }
  .section-preview__item:nth-child(2) {
    padding-top: 5px;
    grid-column: 1/3;
    grid-row: 2/3; }
  .section-preview__item:nth-child(3) {
    grid-column: 1/2;
    grid-row: 3/4; }
  .section-preview__item:nth-child(4) {
    grid-column: 2/3;
    grid-row: 3/4; }
  .section-preview__title {
    font-size: 18px;
    line-height: 18.7px; }
  .section-preview__info {
    font-size: 14px;
    line-height: 16px;
    margin-bottom: 10px; }
  .section-preview__desc {
    font-size: 24px;
    line-height: 26px;
    margin-bottom: 14px; }
  .section-preview__item.one::after {
    right: 7px;
    top: 7px; }
  .section-preview__item.one {
    padding: 236px 21px 28px 28px; }
  .section-preview__desc {
    max-width: 169px; } }
